         linear-gradient(45deg, var(--sl-color-gray-200) 25%, transparent 25%, 
         transparent 75%, var(--sl-color-gray-200) 75%, var(--sl-color-gray-200)),
         linear-gradient(45deg, var(--sl-color-gray-200) 25%, transparent 25%, 
         transparent 75%, var(--sl-color-gray-200) 75%, var(--sl-color-gray-200));
     background-size: 20px 20px;
     background-position: 0 0, 10px 10px;
 }
 .labeled-void .labeled-value-value{
    color:var(--sl-color-gray-300); /* Shoelace color variable */
 }
 .labeled-value-unit{
        font-size: var(--sl-font-size-small); /* Use Shoelace font size variable */
        color: var(--sl-color-neutral-600); /* Use Shoelace color variable */
        margin-left: var(--sl-spacing-x-small); /* Use Shoelace spacing variable */
 }
 .labeled-email .labeled-value-value a{
        color:var(--sl-color-primary-500); /* Shoelace color variable */
        text-decoration: none;
 }
    .labeled-email .labeled-value-value a:hover{
        color:var(--sl-color-primary-700); /* Shoelace color variable */
    }
